html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Rubik", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8; }

p {
  color: #171717;
  font-size: 2rem; }

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto; }

a {
  text-decoration: none; }

img,
picture {
  width: 100%;
  display: block; }

h1, h2, h3 {
  font-family: "Rubik", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center; }

h1 {
  font-size: 3.8rem; }

h2 {
  font-size: 3.4rem; }

h3 {
  font-size: 3rem; }

h4 {
  font-size: 2.6rem; }

.separacion {
  background-color: #171717;
  height: 2rem; }

.seccion {
  min-height: 100vh;
  background-image: linear-gradient(to top, #EDEDED 20%, white 100%); }

/** Media Queries **/
/* Layout */
.header {
  display: grid;
  grid-template-rows: 1fr 1fr 10fr;
  flex-direction: column;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center right;
  height: 100vh; }
  .header h1, .header h2 {
    margin: 0;
    line-height: 1.5; }
  .header h1 {
    color: #FF9F21;
    font-size: 3rem;
    font-family: "Anton", sans-serif; }
    @media (min-width: 678px) {
      .header h1 {
        font-size: 5rem; } }
  .header h2 {
    font-family: "Rubik", sans-serif;
    color: #EDEDED;
    font-size: 2rem; }
    @media (min-width: 678px) {
      .header h2 {
        font-size: 2.5rem; } }

.encabezado {
  background-color: #171717;
  padding: 2.5rem 0; }

.navegacion {
  position: relative;
  background-color: #444444;
  display: flex;
  flex-direction: column;
  padding: 2rem 0; }
  .navegacion .menu {
    display: none; }
    @media (min-width: 678px) {
      .navegacion .menu {
        display: flex;
        justify-content: center;
        flex-direction: row; } }
    .navegacion .menu.mostrar {
      display: flex;
      flex-flow: column; }
      @media (min-width: 678px) {
        .navegacion .menu.mostrar {
          flex-direction: row; } }
  .navegacion a {
    display: block;
    text-align: center;
    margin: 0;
    color: #EDEDED;
    font-size: 1.5rem; }
    @media (min-width: 678px) {
      .navegacion a {
        font-size: 2rem;
        margin-right: 2rem; } }
    .navegacion a:last-of-type {
      margin-right: 0; }
    .navegacion a:hover {
      color: #FF9F21; }
  .navegacion.fijo {
    position: fixed;
    width: 100%;
    box-shadow: 0 .3rem  .4rem  #444; }

.logo {
  width: 4rem; }

.boton-toggle {
  font-size: 3rem;
  color: #EDEDED;
  margin-top: .5rem; }

.menu-movil {
  display: flex;
  justify-content: space-between; }
  @media (min-width: 678px) {
    .menu-movil {
      display: none; } }

.webp .header {
  background-image: url(../../build/img/fondo.webp); }

.nowebp .header {
  background-image: url(../../build/img/fondo.jpg); }

.texto-escritura {
  align-self: center; }
  .texto-escritura span {
    color: #EDEDED;
    font-size: 5rem;
    font-family: "Anton", sans-serif;
    border-bottom: 1rem solid #FF9F21; }

.sobremi {
  padding-bottom: 5rem; }

.seccion-titulo {
  padding: 5rem;
  margin: 0;
  font-family: "Anton", sans-serif;
  font-size: 4rem;
  font-weight: bold;
  color: #FF9F21;
  text-transform: uppercase; }

.informacion {
  width: 100%;
  margin: 0 auto; }
  @media (min-width: 1200px) {
    .informacion {
      width: 70%;
      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: 2rem; } }

.informacion__imagen {
  border-radius: 50%;
  width: 20rem;
  height: 20rem;
  margin: 0 auto; }
  @media (min-width: 678px) {
    .informacion__imagen {
      width: 30rem;
      height: 30rem; } }

.informacion__texto p {
  font-family: "Rubik", sans-serif;
  text-align: center; }

.informacion__texto span {
  color: #FF9F21; }

.tecnologias h3 {
  color: #FF9F21;
  text-transform: uppercase; }

.imagenes {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap; }
  .imagenes img {
    width: 10rem;
    margin-right: 2rem; }
    .imagenes img:last-of-type {
      margin-right: 0; }
  @media (min-width: 678px) {
    .imagenes {
      flex-wrap: nowrap; } }

@media (min-width: 678px) {
  .dos-columnas {
    display: grid;
    grid-template-columns: 1fr 1fr; } }

.aumentar-imagen img {
  width: 15rem;
  margin-top: -35px;
  border-radius: 10px; }

.contenedor-proyectos {
  display: grid;
  row-gap: 2rem; }
  @media (min-width: 678px) {
    .contenedor-proyectos {
      display: grid;
      gap: 2rem;
      grid-template-columns: repeat(2, 1fr); } }
  @media (min-width: 1200px) {
    .contenedor-proyectos {
      display: grid;
      gap: 2rem;
      grid-template-columns: repeat(3, 1fr); } }

.portafolio {
  padding-bottom: 5rem; }

.card__imagen {
  border-radius: 1rem 1rem 0 0; }

.card:hover {
  cursor: pointer; }

.card {
  overflow: hidden; }
  @media (min-width: 1200px) {
    .card:last-of-type {
      grid-column: 2/3; } }

.iconos {
  display: flex;
  justify-content: space-evenly;
  font-size: 4rem;
  padding: 2rem 0;
  color: #FF9F21;
  background-color: #444444;
  border-radius: 0 0 1rem 1rem; }

video {
  display: block;
  width: 100%;
  height: 100%; }

.contactame {
  position: relative;
  max-height: 50rem;
  overflow: hidden; }

.overlay {
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
  align-items: center;
  justify-content: center;
  margin-top: -2.5rem; }
  .overlay h3 {
    padding: 1rem 0; }
    @media (min-width: 678px) {
      .overlay h3 {
        padding: 5rem; } }

.contactos {
  position: relative;
  display: flex;
  justify-content: center;
  flex-wrap: wrap; }
  .contactos img {
    width: 5rem;
    cursor: pointer; }
    @media (min-width: 678px) {
      .contactos img {
        width: 10rem; } }
  .contactos div {
    margin-right: 2.5rem; }
    .contactos div:last-of-type {
      margin-right: 0; }
  .contactos span {
    font-weight: bold;
    position: absolute;
    color: #EDEDED;
    font-size: 1.2rem;
    visibility: hidden;
    opacity: 1;
    transition: visibility 0.5s linear, opacity 0.5s linear; }
    @media (min-width: 678px) {
      .contactos span {
        font-size: 2.5rem; } }

.info-1:first-child {
  margin-left: -1rem;
  margin-top: -2rem; }
  @media (min-width: 678px) {
    .info-1:first-child {
      margin-left: -2rem;
      margin-top: -5rem; } }

.info-2:first-child {
  margin-top: -2rem;
  margin-left: -6rem; }
  @media (min-width: 678px) {
    .info-2:first-child {
      margin-top: -5rem;
      margin-left: -10rem; } }

@media (min-width: 678px) {
  .contenedor-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 2rem; } }

@media (min-width: 678px) {
  .tecnologias__imagenes {
    display: flex;
    justify-content: space-evenly; } }

.tecnologias__imagenes img {
  width: 150px;
  height: 150px;
  margin: 0 auto; }
  @media (min-width: 678px) {
    .tecnologias__imagenes img {
      margin-right: 2rem; }
      .tecnologias__imagenes img:last-of-type {
        margin-right: 0; } }

.tecnologias__imagenes span {
  color: #FF9F21;
  font-size: 3rem;
  text-align: center;
  display: block;
  margin-bottom: 2rem; }

.descripcion {
  margin-top: 3rem; }
  .descripcion a {
    color: red;
    font-size: 2rem; }

.footer {
  background-color: #171717;
  margin-top: 3rem; }
  .footer p {
    padding: 5px 0;
    margin: 0;
    text-align: center;
    font-size: 2.5rem;
    color: #EDEDED; }

.navegacion .LinkInicio {
  font-size: 3rem; }
